/* 定义px转rem规则 */
/* 定义间距尺寸 */
:root {
  --head-top: #f5f5f5;
  --head-tab: #ffffff;
  --head-name: #141414;
  --head-tips: #262626; }

.image-box {
  font-size: 0;
  width: inherit;
  height: inherit; }
  .image-box .width-fixed {
    width: 100%;
    height: auto; }
  .image-box .height-fixed {
    width: auto;
    height: 100%; }
